* {
  color: #333;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}
.container {
  height: 100%;
  max-height: 720px;
  text-align: center;
}
.container .timer-wrapper {
  position: absolute;
  top: 16px;
  left: 16px;
  text-align: left;
}
.container .timer-wrapper .timer {
  display: block;
  font-size: 32px;
}
.container .score-wrapper {
  position: absolute;
  top: 16px;
  right: 16px;
  text-align: right;
}
.container .score-wrapper .score {
  display: block;
  font-size: 32px;
}
.container .card-list {
  width: 100%;
  min-height: 480px;
  position: relative;
  overflow: hidden;
}
.container .card-list .card {
  position: absolute;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  width: 160px;
  height: 80px;
  font-size: 48px;
  line-height: 70px;
  border: 5px solid #666;
  text-align: center;
  -webkit-transition: all 0.45s, border-color 0s;
  -o-transition: all 0.45s, border-color 0s;
  transition: all 0.45s, border-color 0s;
  background: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-touch-callout: none;
  cursor: pointer;
}
.container .card-list .card.placeholder {
  display: none;
}
.container .card-list .card.active {
  border-color: goldenrod;
  bottom: 20px;
}
.container .card-list .card.correct {
  border-color: greenyellow;
}
.container .card-list .card.wrong {
  border-color: orangered;
}
.container .card-list .card.left,
.container .card-list .card.right,
.container .card-list .card.down {
  opacity: 0.4;
}
.container .card-list .card.left {
  left: -160px;
  bottom: 20px;
}
.container .card-list .card.right {
  left: calc(100% +  160px );
  bottom: 20px;
}
.container .card-list .card.down {
  opacity: 0;
  bottom: -100px;
}
.container .card-list .card.pending-1 {
  -webkit-filter: blur(1px);
          filter: blur(1px);
  bottom: 120px;
}
.container .card-list .card.pending-2 {
  -webkit-filter: blur(2px);
          filter: blur(2px);
  bottom: 190px;
}
.container .card-list .card.pending-3 {
  -webkit-filter: blur(3px);
          filter: blur(3px);
  bottom: 240px;
}
.container .card-list .card.pending-4 {
  -webkit-filter: blur(4px);
          filter: blur(4px);
  bottom: 280px;
}
.container .hint-wrapper label {
  display: block;
}
.container .dashboard-wrapper {
  top: 100px;
  position: fixed;
  width: 100%;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  height: 320px;
  padding-top: 140px;
}
.container .dashboard-wrapper.hidden {
  top: -100%;
  opacity: 0;
}
.container .dashboard-wrapper label {
  -webkit-transition: color 1s;
  -o-transition: color 1s;
  transition: color 1s;
  display: block;
  font-size: 48px;
}
.container .dashboard-wrapper label.disabled {
  color: #999;
}
.container .dashboard-wrapper label.frozen-hint {
  color: #999;
  font-size: 24px;
}



